.container{
  width:90%;
  padding:20px;
  margin:100px auto;
  display:flex;
  flex-direction:row;
  justify-content: center;
  margin-bottom: 20px;
}

.box{
  width: 350px;
  margin:0 10px;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, .1);
  transition:ls;
}

.box img{
  display: block;
  width:100%;
  border-radius: 5px;
}

